import React from 'react'
import './About.css'
import { motion } from 'framer-motion'
import aboutbackground from '../Photos/aboutbackground.jpg'

export default function About() {
  return (
    <div className='about'>
      <img src={aboutbackground} alt="" />
      <motion.div
        whileInView={{ y: [-50, 0] }}
      >
        <motion.h1
          whileHover={{ x: 50 }}
          transition={{ duration: 1 }}
        >About Me</motion.h1>
        <p>I am an aspiring full-stack web developer with 1200+ hours of coding experience at Masai School looking for an opportunity to showcase my skills and help the company grow simultaneously.</p>
      </motion.div>
    </div>
  )
}
