import React from 'react'
import './Project.css'
import timelyBanner from '../Photos/timelyBanner.png'
import shinecomBanner from '../Photos/shinecomBanner.png'
import naukricomBanner from '../Photos/nakuricomBanner.png'
import githublogo from '../Photos/githublogo.webp'
import html5logo from '../Photos/html5logo.webp'
import css3logo from '../Photos/css3logo.webp'
import jslogo from '../Photos/jslogo.webp'
import reactlogo from '../Photos/reactlogo.webp'
import chakrauilogo from '../Photos/chakrauilogo.png'
import reactblack from '../Photos/reactblack.png'
import { motion } from 'framer-motion'
import coder from '../Photos/coder.png'

export default function Project() {
  return (
    <div className='project' >
      <motion.h1
        whileHover={{ x: 50 }}
        transition={{ duration: 1 }}> My Projects</motion.h1>
      <div className='projectsContainer' >
        <motion.div
        whileInView={{y:[-50, 0]}}
        >
          <img src={timelyBanner} alt="Timely" />
          <div>
            <h2>Timely Clone</h2>
            <p>Timely is a Norway-based company that is used to track your time.</p>
            <div className='tekstackLive' >
              <div>
                <motion.img whileHover={{ scale: 1.4 }} src={html5logo} alt="HTML" />
                <motion.img whileHover={{ scale: 1.4 }} src={css3logo} alt="CSS" />
                <motion.img whileHover={{ scale: 1.4 }} src={jslogo} alt="JS" />
              </div>
              <div className='projextLinks' >
                <a href="https://github.com/actuallyakshay/Timely.com-clone" target="_blank" ><motion.img whileHover={{ scale: 1.4 }} src={githublogo} alt="GitHub" /></a>
                <a href="https://whimsical-empanada-8d6cc1.netlify.app/" target="_blank" ><span>Live Site</span></a>
              </div>
            </div>
          </div>
        </motion.div>
        <motion.div
        whileInView={{y:[-50, 0]}}
        transition={{delay:0.2}}
        >
          <img src={shinecomBanner} alt="" />
          <h2>Shine.com Clone</h2>
          <p>Shine.com is India's leading online job and recruitment portal.</p>
          <div className='tekstackLive' >
            <div>
              <motion.img whileHover={{ scale: 1.4 }} src={html5logo} alt="HTML" />
              <motion.img whileHover={{ scale: 1.4 }} src={css3logo} alt="CSS" />
              <motion.img whileHover={{ scale: 1.4 }} src={jslogo} alt="JS" />
              <motion.img whileHover={{ scale: 1.4 }} src={reactblack} alt="React" />
            </div>
            <div className='projextLinks' >
              <a href="https://github.com/Prakashkumarjena700/Shine.com_Clone/tree/main/my-app" target="_blank" ><motion.img whileHover={{ scale: 1.4 }} src={githublogo} alt="GitHub" /></a>
              <a href="https://rococo-fairy-d1a23d.netlify.app/" target="_blank" ><span>Live Site</span></a>
            </div>
          </div>
        </motion.div>
        <motion.div
        whileInView={{y:[-50, 0]}}
        >
          <img src={naukricomBanner} alt="" />
          <h2>Naukri.com Clone</h2>
          <p>Naukri.com is an Indian employment website operating in India and</p>
          <div className='tekstackLive' >
            <div>
              <motion.img whileHover={{ scale: 1.4 }} src={html5logo} alt="HTML" />
              <motion.img whileHover={{ scale: 1.4 }} src={css3logo} alt="CSS" />
              <motion.img whileHover={{ scale: 1.4 }} src={jslogo} alt="JS" />
              <motion.img whileHover={{ scale: 1.4 }} src={reactblack} alt="React" />
              <motion.img whileHover={{ scale: 1.4 }} src={chakrauilogo} alt="Chakra" />
            </div>
            <div className='projextLinks' >
              <a href="https://github.com/Prakashkumarjena700/righteoussilk4795/tree/main/my-app" target="_blank" ><motion.img whileHover={{ scale: 1.4 }} src={githublogo} alt="GitHub" /></a>
              <a href="https://glittering-entremet-c52678.netlify.app/" target="_blank" ><span>Live Site</span></a>
            </div>
          </div>
        </motion.div>
        <motion.div
         whileInView={{y:[-50, 0]}}
         transition={{delay:0.2}}
        >
          <img src={coder} alt="" />
        </motion.div>
      </div>
    </div>
  )
}
